@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --grey: #626265;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a:link,
a:visited,
a:active {
  text-decoration: none;
  color: rgb(166,1,69);
}

button:hover{
  cursor: pointer;
}

li{
  list-style: none;
}

li:hover{
  border-bottom: 3px solid rgb(166,1,69);
}

.row {
  display: flex;
  flex-direction: row;
}

.column{
  display: flex;
  flex-direction: column;
}

.center{
  display: flex;
  justify-content: center;
  align-items: center;
}

.text-grey{
  font-family: 'Roboto', sans-serif;
  color: #626265;
  color: var(--grey)
}
.footer{
    padding: 1.5% 3%;
    justify-content: space-between;
}

.text-footer{
    flex:4 1;
    justify-content: initial;
    align-items: center;
    display: flex;
}
.container-menu{
    justify-content: space-evenly;
    padding: 2% 0;
}

.li-active{
    border-bottom: 3px solid rgb(166,1,69);   
}

.header{
    padding: 1% 2%;
}

.principal-container{
    justify-content: space-between;
    align-items: center;
}

.logo{
    height: 45px;
}
.container-info{
    padding: 2% 5%;
    min-height: 600px;
    justify-content: space-evenly;
}

.font-nunito{
    font-family: 'Nunito', sans-serif;
    line-height:3;
}

.h50{
    display: flex;
    justify-content: flex-end;
    height: 50px;
}
.container-cards{
    background: rgb(249,248,253);
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    min-height: 550px;
}
.card{
    border-radius: 10px;
    border: 1px solid lightskyblue;
    background: white;
    padding: 1%;
    margin: 1.5%;
    height: 200px;
    width: 400px;
    justify-content: space-evenly;
}

.container-icono{
    display: flex;
    justify-content: space-between;
    height: 25px;
}

.icono{
    height: 100%;
}

.text-card{
    font-family: 'Roboto', sans-serif;
    font-size: 13px;
}

.h-20{
    height: 20px;
    justify-content: space-between;
}

.container-percent{
    min-width: 55px;
    justify-content: space-between;
}

.small-leter{
    font-size: 10px;
}

.filter{
    flex-wrap: wrap;
    justify-content: space-evenly;
    width: 100%;
    padding: 1%;
    border-top: 2px solid rgb(249,248,253);
}

.container-sort{
    flex-wrap: wrap;
    justify-content: space-evenly;
    border-top: 2px solid rgb(249,248,253);
    flex: 2.5 1;
}

.container-filter{
    flex: 1 1;
}
.btn{
    background: none;
    padding: 0.5% 2.5%;
    border-radius: 20px;
    border: 2px solid lightskyblue;
    transition: all 1s ease;
}

.btn:hover {
    -webkit-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
    background: lightskyblue;
    color: #fff;
}

.btn-active {
    -webkit-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
    background: lightskyblue;
    color: #fff;
}

.btn-bordo{
    border: 2px solid rgb(166,1,69);
    height: 100%;
}

.btn-bordo:hover {
    -webkit-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
    background: rgba(150, 107, 125, 0.529);
    color: #fff;
}

.text-16{
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 600;
}

  
.w100{
width: 100%;
}
